import { useState } from 'react';
import { useCreateContactMutation, useFetchContactsQuery } from '../../Store/contactsSlice';

import { Form, Label, Input } from './ContactsForm.styled';
import { Button } from '../Buttons/Buttons.styled';
import toast, { Toaster } from 'react-hot-toast';

function ContactsForm() {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const [createContact] = useCreateContactMutation();
  const {data: contacts} = useFetchContactsQuery();

  const handleChange = (e) => {
    switch (e.target.name) {
      case 'name':
        setName(e.target.value);
        break;
      case 'number':
        setNumber(e.target.value);
        break;
      default:
        return;
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // checkContact();
    // setName('');
    // setNumber('');
  
  const checkContact = contacts.find(
    (contact) => contact.name.toLowerCase() === name.toLowerCase()
  );
  if (checkContact) {
    // alert(`${name} is already in contacts`
    // notification = (name) =>
    toast(`${name} is already in contacts`, {
      icon: 'üëè',
      style: {
        borderRadius: '10px',
        background: '#333',
        color: '#fff',
        duration: 4000,
      },
    });
    // setName("");
    // setNumber("");
    return;
    };
    
    createContact({ name, number });
    setName("");
    setNumber("");
  };

  // const newContact = () => {
  //   const contactToAdd = {
  //     name,
  //     number,
  //   };
  //   stateContact.some(
  //     contact => contact.name.toLowerCase() === contactToAdd.name.toLowerCase(),
  //   )
  //     ? notification(contactToAdd.name)
  //     : dispatch(addContact(contactToAdd));
  // };

  // const notification = (name) =>
  //   toast(`${name} is already in contacts`, {
  //     icon: 'üëè',
  //     style: {
  //       borderRadius: '10px',
  //       background: '#333',
  //       color: '#fff',
  //       duration: 4000,
  //     },
  //   });

  return (
    <>
      <Form action="" onSubmit={handleSubmit}>
        <Label htmlFor="name">Name</Label>
        <Input
          onChange={handleChange}
          type="text"
          name="name"
          value={name}
          pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
          title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
          required
        />
        <Label htmlFor="number">Number</Label>
        <Input
          onChange={handleChange}
          type="tel"
          name="number"
          value={number}
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
          required
        />
        <Button type="submit"> Add contact</Button>
      </Form>
      <Toaster />
    </>
  );
}

